import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

export default class Col extends React.PureComponent {

  constructor(props) {
    super(props);

    this.CONSTANT_FLEX = {
      'fixed': 'fixed',  // 
      'grow': 'grow-only', // 
      'growAndShrink': 'grow-and-shrink', // 
      'shrink': 'shrink-only' // 
    };
  }

  static propTypes = {
    flex: PropTypes.oneOf(['fixed', 'grow', 'growAndShrink', 'shrink']),
    className: PropTypes.string
  }

  static defaultProps = {
    flex: 'shrink',
    className: ''
  }

  render() {
    let {
      flex,
      children,
      className
    } = this.props;
    
    return <div className={classnames('col', this.CONSTANT_FLEX[flex], className)}>
        { children }
      </div>
  }
}



